body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Container_container__2tSdh {
    min-height: 100vh;
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Container_main__3JZRe {
    background-color: oldlace;
}

.Container_secondary__1fkoK {
    background-color: #333;
}
:root {
    --main-text-color: #333;
    --secondary-text-color: oldlace;
}

h1, h2 {
    font-family: 'Fredericka the Great', cursive;
}
h3 {
    font-family: 'Niconne', cursive;
    /* font-family: 'Sacramento', cursive; */
}

p {
    font-family: 'Open Sans Condensed', sans-serif;
    text-align: center;
    padding: 1em 50px;
    margin: 10px;
}

div[class*="Container_main__"] h1, div[class*="Container_main__"] h2, div[class*="Container_main__"] h3, div[class*="Container_main__"] p {
    color: var(--main-text-color);
}

div[class*="Container_secondary__"] h1, div[class*="Container_secondary__"] h2, div[class*="Container_secondary__"] h3, div[class*="Container_secondary__"] p {
    color: var(--secondary-text-color);
}
